/*////// Generals///////////////////////////////////////////// */
body, html {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
}
h1 {
  --min-font: 2rem;
  font-size: max(3.5vw, var(--min-font));
}
h2 {
  --min-font: 1.5rem;
  font-size: max(2.5vw, var(--min-font));
}
p {
  --min-font: 0.8rem;
  font-size: max(1rem, var(--min-font));
}


@media (max-width: 600px) {
  h1 {
    font-size: 2rem;
  }

  h2 {
    font-size: 1.5rem;
  }

  p {
    font-size: 0.8rem;
  }
}
h1,
h2,
p {
  margin-top: 0;
  margin-bottom: 0;
}

img {
  display: block;
  margin: 0;
  padding: 0;
  width: 100%;
}

*,
*::before,
*::after {
  color:white;
}

.page {
  --color: #000000;
  background-color: var(--color);
}

.page > * {
  padding: 0px 10% 0px 10%;
}

.clic, .clic-white {
  cursor: pointer;
}

.clic:hover {
  filter: brightness(70%);
}

.clic-white:hover {
  background-color: #adadad;
}

.container {
  padding:5vh 1% 5vh 1%;
}
.border-animation-patch {
  --color: #000000;
  background-color: var(--color);
}

.border-animation-patch:before{
  content: '';
  position: absolute;
  top: 0px;
  left: 0px;
  width: 25vw;
  height: 50vh;
  background-color: #000000;
}

.border-animation-patch:after{
  content: '';
  position: absolute;
  right: 0px;
  bottom: 0px;
  width: 25vw;
  height: 50vh;
  background-color: #000000;
}

.border-animation {
  --border-width: 0.2em;
  --animation-speed: 10s;
  --color: #ffffff;
  background-color: var(--color);
  border: var(--border-width) solid transparent;
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  pointer-events: none;
  z-index: 0;
}

.border-animation__inner {
  position: relative;
  height: 85vh;
  padding: 0 10%;
  background: #000000;
  background-image: url(https://www.dropbox.com/scl/fi/h5k8ywumwkb9haenbasfi/fondo.png?rlkey=9pbcw2hj6zf4z5aodjgvzq0s7&st=fa6t43j3&raw=1);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  z-index: 1;
}

.border-animation:before{
  content: '';
  position: absolute;
  top: 10vh;
  left: 0;
  width: 2vw;
  height: 40vh;
  background-color: rgb(0, 0, 0);
}

.border-animation:after{
  content: '';
  position: absolute;
  bottom:  0;
  right: 0;
  width: 1.5vw;
  height: 40vh;
  background-color: rgb(0, 0, 0);
}

@keyframes beforeBorders {
  0% {
    opacity: 0;
  }

  1.01% {
    opacity: 1;
  }

  82% {
    width: 2vw;
    height: 40vh;
  }

  85% {    
     width: 1.5vw;
     height: 85vh; 
  }
  90% {
    width: 90vw;
    height: 85vh; 
  }

  90.01% {
    transform: translateY(0);
  }
  
  95% {
    width: 96vw;
    height: 1vh;
    transform: translateY(80vh);
  }

  99.99% {
    opacity: 1;
    width: 5vw;
    height: 1vh; 
    transform: translateY(80vh) translateX(90vw);
  }
  100% {
    opacity: 0;
  }
}


@keyframes afterBorders {
  0% {
    opacity: 0;
  }

  1.01% {
    opacity: 1;
  }

  82% {
    width: 1.5vw;
    height: 40vh;
  }
   85% {    
     height: 87vh; 
     transform: translateY(-10vh);
  }
  87% {
    width: 1.5vw;
    height: 87vh; 
    transform: translateY(-10vh);
  }

  90% {
    height: 87vh; 
    width: 90vw;
  }

  90.01% {
    height: 87vh; 
    width: 90vw;
    transform: translateY(-10vh);
  }
  95% {
    width: 90vw;
    height: 4vh; 
    transform: translateY(-91.5vh);
  }
  98% {
    opacity: 1;
    width: 2vh;
    height: 4vh; 
    transform: translateX(-91vw)  translateY(-91.5vh) ;
  }
  99.99% {
    opacity: 1;
    width: 2vh;
    height: 4vh; 
    transform: translateX(-91vw)  translateY(-91.5vh) ;
  }
  100% {
    opacity: 1;
  }
}

@media (min-width: 600px) {
  .border-animation:after {
    animation: afterBorders var(--animation-speed) forwards infinite ease-in-out;
  }
}

@media (min-width: 600px) {
  .border-animation:before {
    animation: beforeBorders var(--animation-speed) forwards infinite ease-in-out;
  }
}

.img-size {
  position: relative;
  overflow:hidden;
  width: var(--w);
  height: var(--h);
}

.black {
  color: black;
  background-color: white;
}
