  top: 75vh;
  left: var(--vvwa);
  width: 40px;
  height: 40px;
  opacity: 0;
  background-color: var(--col4);
  transform: rotate(5deg);
  animation: cubeFloating4 6s ease-in-out infinite;
}

@media (max-width: 600px) {
  .block-effect::after {
    left: 80vw;
  }
  .block-effect::before{
    left: 60vw;
  }
}

@keyframes cubeFloating1 {
  0%{
    transform: translate(0px, 0px);
    opacity: 0;
    transform: scale(1);
  }
  20%{
    transform: translate(0px, 0px);
    opacity: 0;
    transform: scale(1);
  }
  50%{
    opacity: .4;
  }
  70%{
    transform: translate(30px, -80px) rotate(-90deg) scale(1.5);
    opacity: 0;
  }
  100%{
    transform: translate(30px, -80px) rotate(-90deg) scale(1.5);
    opacity: 0;
    transform: scale(1);
  }
}

@keyframes cubeFloating2 {
  0%{
    transform: translate(0px, 0px);
    opacity: 0;
    transform: scale(1);
  }
  30%{
    transform: translate(0px, 0px);
    opacity: 0;
    transform: scale(1);
  }
  40%{
    opacity: .4;
  }
  80%{
    transform: translate(-10px, -80px) rotate(-40deg) scale(1.5);
    opacity: 0;
  }
}

@keyframes cubeFloating3 {
  0%{
    transform: translate(0px, 0px);
    opacity: 0;
    transform: scale(1);
  }
  40%{
    transform: translate(0px, 0px);
    opacity: 0;
    transform: scale(1);
  }
  70%{
    opacity: .4;
  }
  100%{
    transform: translate(50px, -130px) rotate(40deg) scale(1.5);

  }
}

@keyframes cubeFloating4 {
  0%{
    transform: translate(0px, 0px);
    opacity: 0;
    transform: scale(1);
  }
  50%{
    transform: translate(0px, 0px);
    opacity: 0;
    transform: scale(1);
  }
  80%{
    opacity: .4;
  }
  100%{
    transform: translate(-30px, -60px) rotate(-10deg) scale(1.5);
  }
}

.point {
  opacity: 0;
  transition: opacity 1s ease;
}

.point.visible {
  opacity: 1;
}

.hidden {
  opacity: 0;
}

.block-reveal {
  opacity: 1;
  --t: calc(var(--td) + var(--d));
  display: flex;
  color: transparent;
 
  padding-top: 30px;
  position: relative;
  overflow: hidden;

  animation: revealBlock 0s var(--t)  forwards;
}

.block-reveal::after {
  content: '';

  width: 0%;
  height: 100%;
  padding-bottom: 4px;

  position: absolute;
  top: 0;
  right: 0;

  background: var(--bc);
  animation: revealingIn var(--td) var(--d)  forwards, revealingOut var(--td) var(--t) forwards;
}

@keyframes revealBlock {
  100% {
    color: #fafafa;
  }
}

@keyframes revealingIn {
  0% {
    width: 0;
    transform: translateX(100%); 
  }

  100% {
    width: 100%; 
    transform: translateX(0);
  }
}

@keyframes revealingOut {
  0% {
    transform: translateX(0); 
  }
  50% {
    overflow: hidden;
  }
  100% {
   
    transform: translateX(-110%);
  }
}
