.ball-bouncing {
  opacity: 0;
  transition: opacity 4s ease;
  background-color: var(--lemon-chiffon);
}

.ball-bouncing::before {
  animation: shadowBouncing 1.2s ease-in infinite;
  background-color: rgb(255, 255, 255);
  border-radius: 1rem;
  bottom: .4rem;
  right: .6rem;
  content: '';
  height: 0.12rem;
  position: absolute;
  width: 1.2rem;
}

.ball {
  right: .4rem;
  background-color: var(--color);
  border-radius: 50%;
  height: 1.5rem;
  position: absolute;
  width: 1.5rem;
}

.ball-bouncing .ball  {
  animation: ballBouncing 1.2s ease-in infinite;
  bottom: .8rem;
  transform-origin: bottom;
}

.ball-bouncing.visible {
  opacity: 1;
}

@keyframes ballBouncing {
  0%,
  100% {
    transform: scale(1.2, 0.2);
    
  }

  20% {
    transform: scaleY(1.2);
  }
  20%,
  80% {
    transform: translateY(-1.4rem);
  }
  60% {
    transform: translateY(-1.5rem);
  }
  90% {
    transform: translateY(20%);
  }
}

@keyframes shadowBouncing {
  0%,
  100% {
    transform: scale(2, 0.8);
  }
  20% {
    transform: scale(1.2, 0.8);
  }
  40%,
  80% {
    transform: scale(0.5, 0.2);
  }
  70% {
    transform: scale(0.5, 0.15);
  }
  90% {
    transform: scale(1.5, 0.6);
  }
}




