.button-animation {
  --border-width: 0.3em; /* Ancho del borde */
  --animation-speed: 0.5s; /* Velocidad de la animación */
  --color: #ffffff; /* Color principal */
  color: var(--color); /* Color del texto usando la variable */
  position: relative; /* Posición relativa para alinear elementos hijos */
  display: inline-block; /* Mostrar como bloque en línea */
  line-height: 1em; /* Altura de línea */
  transform: scale(1, 0.8); /* Escala para efecto de tamaño */
  border: var(--border-width) solid transparent; /* Borde sólido transparente */
}

.button-animation__inner {
  position: relative; /* Posición relativa para alinear elementos hijos */
  display: inline-block; /* Mostrar como bloque en línea */
  cursor: pointer; /* Estilo de cursor */
  padding: 0.2em; /* Relleno alrededor del texto */
  background: rgba(0, 0, 0, 0.5); /* Fondo con transparencia */
  z-index: 1; /* Orden de apilamiento */
  border: solid var(--border-width) transparent; /* Borde sólido transparente */
}

.button {
  background-color: transparent;
  font-family: 'Six Caps', sans-serif; /* Familia de fuente */
  cursor: pointer; /* Estilo de cursor */
  font-weight: 300; /* Peso de la fuente */
  text-transform: uppercase; /* Convertir texto a mayúsculas */
  letter-spacing: 0.05em; /* Espaciado entre letras */
}
.button-animation::before,
.button-animation::after {
  content: '';
  position: absolute;
  background: var(--color);
}
/* Animación para borde antes del botón */
.button-animation:focus:before,
.button-animation:hover:before {
  animation: beforeButtons var(--animation-speed) forwards ease-in-out;
}

/* Animación para borde después del botón */
.button-animation:focus:after,
.button-animation:hover:after {
  animation: afterButtons var(--animation-speed) forwards ease-in-out;
}

/* Animación para cambio de color del botón al enfocar o pasar el mouse */
.button-animation:focus,
.button-animation:hover {
  animation: ButtonColors var(--animation-speed) forwards ease-in-out; /* Cambiado a ease-in-out */
  outline: none; /* Eliminar contorno por defecto */
}

/* Animación para cambio de fondo del contenido interno */
.button-animation:focus .button-animation__inner,
.button-animation:hover .button-animation__inner {
  animation: background calc(var(--animation-speed) / 5 * 3) forwards ease-in-out;
  animation-delay: calc(var(--animation-speed) / 5 * 2); /* Retraso de la animación */
}

/* Keyframes para animación: expandir borde desde arriba */
@keyframes beforeButtons {
  0% {
    top: calc(var(--border-width) * -1);
    left: 50%;
    bottom: auto;
    right: auto;
    width: 0;
    height: var(--border-width);
  }
  33% {
    top: calc(var(--border-width) * -1);
    left: calc(var(--border-width) * -1);
    bottom: auto;
    right: auto;
    width: calc(50% + var(--border-width));
    height: var(--border-width);
  }
  66% {
    top: calc(var(--border-width) * -1);
    left: calc(var(--border-width) * -1);
    bottom: auto;
    right: auto;
    width: var(--border-width);
    height: calc(100% + var(--border-width) * 2);
  }
  100% {
    top: auto;
    left: calc(var(--border-width) * -1);
    bottom: calc(var(--border-width) * -1);
    right: auto;
    width: calc(50% + var(--border-width));
    height: calc(100% + var(--border-width) * 2);
  }
}

/* Keyframes para animación: expandir borde desde abajo */
@keyframes afterButtons {
  0% {
    top: calc(var(--border-width) * -1);
    left: auto;
    bottom: auto;
    right: 50%;
    width: 0;
    height: var(--border-width);
  }
  33% {
    top: calc(var(--border-width) * -1);
    left: auto;
    bottom: auto;
    right: calc(var(--border-width) * -1);
    width: calc(50% + var(--border-width));
    height: var(--border-width);
  }
  66% {
    top: calc(var(--border-width) * -1);
    left: auto;
    bottom: auto;
    right: calc(var(--border-width) * -1);
    width: var(--border-width);
    height: calc(100% + var(--border-width) * 2);
  }
  100% {
    top: auto;
    left: auto;
    bottom: calc(var(--border-width) * -1);
    right: calc(var(--border-width) * -1);
    width: calc(50% + var(--border-width));
    height: calc(100% + var(--border-width) * 2);
  }
}

/* Keyframes para animación: cambiar colores del botón */
@keyframes ButtonColors {
  0% {
    border-top-color: transparent;
    border-right-color: transparent;
    border-bottom-color: transparent;
    border-left-color: transparent;
  }
  33% {
    border-top-color: var(--color);
    border-right-color: transparent;
    border-bottom-color: transparent;
    border-left-color: transparent;
  }
  66% {
    border-top-color: var(--color);
    border-right-color: var(--color);
    border-bottom-color: transparent;
    border-left-color: var(--color);
  }
  100% {
    border-top-color: var(--color);
    border-right-color: var(--color);
    border-bottom-color: var(--color);
    border-left-color: var(--color);
  }
}

/* Keyframes para animación: cambiar fondo */
@keyframes background {
  to {
    background: #222222;
    text-shadow: 0 0.1em 0.1em #0c0c0c;
  }
}

/* Media query para pantallas más grandes */
@media (min-width: 850px) {
  body {
    justify-content: center; /* Centrar contenido */
  }
}


